.quarter-plan {
	padding-top: 36px;
	padding-bottom: 98px;
	.container:first-of-type {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex; } }
.quarter-plan__h2 {
	width: 150px;
	margin-top: 57px;
	margin-bottom: 36px; }
.quarter-plan__instruction {
	p {
		color: #222;
		font: 400 16px/24px $font-family;
		padding-left: 50px;
		position: relative;
		&:first-of-type {
			margin-bottom: 27px;
			&:before {
				content: '1'; } }
		&:before {
			text-align: center;
			content: '2';
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			color: white;
			font: 700 24px $font-family;
			background: #375272;
			width: 40px;
			height: 40px;
			border-radius: 50%; } } }
.quarter-plan__map {
	position: relative;
	width: 644px;
	height: 429px;
	z-index: 1; }



html.magnifying > body {
	overflow-x: hidden !important; }
.magnify {
	position: relative;
	display: inline-block; }
.magnify .magnify-lens {
	position: absolute;
	width: 126px;
	height: 126px;
	border-radius: 100%;
	box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.85);


	cursor: none;
	display: none;
	z-index: 100; }

.magnify .magnify-lens.loading {
	background: #333 !important;
	opacity: 0.8; }
.magnify .magnify-lens.loading:after {
	position: absolute;
	top: 45%;
	left: 0;
	width: 100%;
	color: #fff;
	content: 'Loading...';
	font: italic normal 16px/1 Calibri, sans-serif;
	text-align: center;
	text-shadow: 0 0 2px rgba(51, 51, 51, 0.8);
	text-transform: none; }
